    ownerName() {
      return this.userConfig?.ownerName || 'Sanyue';
    },
    isDark() {
      return this.$store.getters.useDarkMode;
    }
  },
  watch: {
    isDark: {
      immediate: true,
      handler(val) {
        document.documentElement.classList[val ? 'add' : 'remove']('dark-mode');
      }
